<template>
  <h2>Í∏ÄÏì∞Í∏∞</h2>

  <form @submit.prevent="writeVisitor">
    <label for="">Ï†úÎ™©</label>
    <el-input
      v-model="visitors.title"
      maxlength="30"
      placeholder="Ï†úÎ™©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî"
      show-word-limit
      type="text"
    />

    <label for="">Î∞©Î™ÖÎ°ù</label>
    <el-input
      v-model="visitors.content"
      maxlength="500"
      placeholder="Î∞©Î™ÖÎ°ùÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî"
      show-word-limit
      type="textarea"
      :rows="10"
      :autosize="{ minRows: 20, maxRows: 200 }"
    />

    <label for="text">ÎπÑÎ∞ÄÎ≤àÌò∏</label>
    <el-input
      v-model="visitors.password"
      type="text"
      placeholder="ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî"
    />

    <div>
      <el-checkbox v-model="visitors.isPrivate">ÎπÑÎ∞ÄÍ∏Ä</el-checkbox>
    </div>
    <button type="submit" class="submit-button">ÏûëÏÑ±</button>
  </form>
</template>
  
  <script>
import axios from "axios";

export default {
  name: "PageVisitorWrite",
  data() {
    return {
      visitors: {
        title: "",
        content: "",
        password: "",
        isPrivate: false,
      },
    };
  },
  methods: {
    async writeVisitor() {
      try {
        const response = await axios.post(
          `${process.env.VUE_APP_BACKEND_URL}/api/visitor`,
          this.visitors,
          {
            withCredentials: true,
          }
        );
        if (response.status === 201) {
          alert("Î∞©Î™ÖÎ°ù ÏûëÏÑ±Ïóê ÏÑ±Í≥µÌñàÏäµÎãàÎã§.");
          this.$router.push("/visitor");
        }
      } catch (error) {
        let errorMessage = "Î∞©Î™ÖÎ°ù ÏûëÏÑ±Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.";

        if (error.response && error.response.data) {
          errorMessage = Array.isArray(error.response.data.errorMessage)
            ? error.response.data.errorMessage.join("\n")
            : error.response.data.errorMessage || errorMessage;
        }

        alert(errorMessage);
        console.error(error, "Ïïå Ïàò ÏóÜÎäî ÏóêÎü¨ Î∞úÏÉù");
      }
    },
  },
};
</script>
