<template>
  <form @submit.prevent="login" class="login-form">
    <h1>Login</h1>

    <input
      type="email"
      v-model="loginInfo.email"
      placeholder="Email"
      required
      class="input-field"
    />
    <input
      type="password"
      v-model="loginInfo.password"
      placeholder="Password"
      required
      class="input-field"
    />
    <button type="submit" class="submit-button">Login</button>
  </form>
</template>

<script>
import axios from "axios";

export default {
  name: "PageLogin",
  data() {
    return {
      loginInfo: {
        email: "",
        password: "",
      },
    };
  },
  methods: {
    async login() {
      try {
        const response = await axios.post(
          `${process.env.VUE_APP_BACKEND_URL}/api/user/login`,
          this.loginInfo,
          {
            withCredentials: true,
          }
        );
        if (response.status === 200) {
          alert("Î°úÍ∑∏Ïù∏Ïóê ÏÑ±Í≥µÌñàÏäµÎãàÎã§.");
          this.$store.dispatch("login");
          this.$router.push("/home");
        } else {
          alert(response.data.errorMessage || "Î°úÍ∑∏Ïù∏Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
        }
      } catch (error) {
        alert("Î°úÍ∑∏Ïù∏Ïù¥ Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
        console.error(error, "Ïïå Ïàò ÏóÜÎäî ÏóêÎü¨ Î∞úÏÉù");
      }
    },
  },
};
</script>

<style scoped>
h1 {
  font-size: 2rem;
  margin-bottom: 10px;
}
.login-form {
  flex-direction: column;
  padding: 20px;
  max-width: 300px;
  margin: auto;
}

.input-field {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.submit-button {
  padding: 10px 20px;
  color: white;
  background-color: #4286f4;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 10px;
}

.submit-button:hover {
  background-color: #3066be;
}
</style>