<template>
  <div class="boardTitle">
    <h2>{{ subCategoryName }}</h2>
    <el-button type="primary" @click="goToWritePage">Í∏ÄÏì∞Í∏∞</el-button>
  </div>

  <el-table :data="posts" class="table-responsive" style="width: 100%">
    <el-table-column prop="index" label="Î≤àÌò∏" width="80">
      <template #default="{ $index }">
        {{ calculateIndex($index) }}
      </template>
    </el-table-column>
    <el-table-column label="Ï†úÎ™©">
      <template #default="{ row }">
        <router-link
          :to="`/post/${row.id}`"
          @click="BoardViewRequest(row.id)"
          >{{ row.title }}</router-link
        >
      </template>
    </el-table-column>
    <el-table-column prop="user.name" label="ÏûëÏÑ±Ïûê" width="100" />
    <el-table-column
      prop="createdAt"
      label="ÏûëÏÑ±Ïùº"
      width="150"
      :formatter="formatDate"
    />
    <el-table-column prop="views" label="Ï°∞ÌöåÏàò" width="80" />
  </el-table>

  <Pagination
    :total-pages="meta.totalPages"
    :current-page="currentPage"
    @page-changed="changePage"
  />
</template>

<script>
import axios from "axios";
import Pagination from "../common/PaginationButton.vue";

export default {
  name: "pageBoard",
  components: {
    Pagination,
  },
  data() {
    return {
      posts: [],
      meta: {
        totalPages: 0,
        currentPage: 1,
        totalCount: 0,
        pageSize: 15,
      },
      subCategoryName: "",
      status: "",
    };
  },
  created() {
    const pageFromUrl = this.$route.query.page || 1;
    this.currentPage = Number(pageFromUrl);
    this.fetchPosts(this.$route.params.subCategoryId);
    this.checkAdminStatus();
  },
  // ÌéòÏù¥ÏßÄ Î≥ÄÍ≤Ω Í∞êÏãú
  watch: {
    "$route.params.subCategoryId": {
      immediate: true,
      handler(newVal) {
        if (newVal) {
          // newValÏù¥ Ïú†Ìö®Ìïú Í∞íÏùº ÎïåÎßå fetchPosts Ìò∏Ï∂ú
          this.fetchPosts(newVal);
        }
      },
    },
    "$route.query.page": function (newPage) {
      this.currentPage = Number(newPage) || 1;
      if (this.$route.params.subCategoryId) {
        // subCategoryIdÍ∞Ä Ïú†Ìö®Ìïú Í∞íÏùº ÎïåÎßå fetchPosts Ìò∏Ï∂ú
        this.fetchPosts(this.$route.params.subCategoryId);
      }
    },
  },
  methods: {
    // api ÏöîÏ≤≠ : Í≤åÏãúÌåê Î†åÎçîÎßÅ
    async fetchPosts(subCategoryId) {
      try {
        if (!subCategoryId) return;

        const pageFromUrl = this.$route.query.page || 1;
        const response = await axios.get(
          `${process.env.VUE_APP_BACKEND_URL}/api/post?page=${pageFromUrl}&subCategoryId=${subCategoryId}`
        );
        this.subCategoryName = response.data.subCategory.name;
        this.posts = response.data.posts;
        this.meta = response.data.meta;
        this.totalCount = response.data.meta.totalCount;
      } catch (error) {
        console.error("Error fetching posts", error);
      }
    },
    // api ÏöîÏ≤≠: Ïú†Ï†Ä ÏÉÅÌÉú ÌôïÏù∏
    async checkAdminStatus() {
      if (!this.getCookie("Authorization")) {
        return; // Ïø†ÌÇ§Í∞Ä ÏóÜÏúºÎ©¥ Ìï®Ïàò Ï¢ÖÎ£å
      }

      try {
        const response = await axios.get(
          `${process.env.VUE_APP_BACKEND_URL}/api/user/userInfo`,
          {
            withCredentials: true,
          }
        );
        console.log(this.status);
        this.status = response.data.status;
      } catch (error) {
        console.log("ÎØ∏Î°úÍ∑∏Ïù∏ ÏÉÅÌÉú");
      }
    },

    // api ÏöîÏ≤≠ : Ï°∞ÌöåÏàò
    async BoardViewRequest(postId) {
      const viewedCookie = this.getCookie(`postViewed_${postId}`);
      if (!viewedCookie) {
        try {
          await axios.post(
            `${process.env.VUE_APP_BACKEND_URL}/api/view/post/${postId}`
          );
          this.setCookie(`postViewed_${postId}`, "viewed", 1); // 1Ïùº ÎèôÏïà Ïú†Ìö®
        } catch (error) {
          console.error("Error sending view request", error);
        }
      }
    },
    // ÎÇ†Ïßú Îç∞Ïù¥ÌÑ∞
    formatDate(row, column) {
      const date = new Date(row[column.property]);
      return date.toLocaleDateString();
    },
    // ÏÉÅÏÑ∏ÌéòÏù¥ÏßÄ Ïù¥Îèô
    goToWritePage() {
      if (this.status !== "admin") {
        alert("Í∂åÌïúÏù¥ ÏóÜÏäµÎãàÎã§.");
        return;
      }

      const subCategoryId = this.$route.params.subCategoryId;
      this.$router.push({ path: "/board/write", query: { subCategoryId } });
    },
    // ÌéòÏù¥ÏßÄ Î≥ÄÍ≤Ω
    changePage(page) {
      this.currentPage = page;
      this.fetchPosts(this.$route.params.subCategoryId);
      this.$router.push({
        path: this.$route.path,
        query: { page: page.toString() },
      });
    },
    // Í≤åÏãúÍ∏Ä Î≤àÌò∏ index Í≥ÑÏÇ∞
    calculateIndex(index) {
      return this.totalCount - (this.currentPage - 1) * 15 - index;
    },

    // Ïø†ÌÇ§ ÏÑ§Ï†ï
    setCookie(name, value, days) {
      let expires = "";
      if (days) {
        let date = new Date();
        date.setTime(date.getTime() + days * 24 * 60 * 60 * 1000);
        expires = "; expires=" + date.toUTCString();
      }
      document.cookie = name + "=" + (value || "") + expires + "; path=/";
    },

    // Ïø†ÌÇ§ Í∞ÄÏ†∏Ïò§Í∏∞
    getCookie(name) {
      let nameEQ = name + "=";
      let ca = document.cookie.split(";");
      for (let i = 0; i < ca.length; i++) {
        let c = ca[i];
        while (c.charAt(0) === " ") c = c.substring(1, c.length);
        if (c.indexOf(nameEQ) === 0)
          return c.substring(nameEQ.length, c.length);
      }
      return null;
    },
  },
};
</script>

<style scoped>
.boardTitle {
  display: flex;
  justify-content: space-between;
}

h2 {
  margin-bottom: 30px;
}
</style>
