<template>
  <router-link to="/home" class="custom-router-link">
    <h1>w-life daily</h1>
  </router-link>
  <div>
    <router-link v-if="!isLoggedIn" to="/login">
      <el-button type="Login" round>Login</el-button>
    </router-link>
    <router-link v-if="!isLoggedIn" to="/register">
      <el-button type="Register" round>Register</el-button>
    </router-link>

    <router-link v-if="isLoggedIn" to="/logout">
      <el-button type="Logout" @click="logout" round>Logout</el-button>
    </router-link>
    <router-link v-if="isLoggedIn" to="/account">
      <el-button type="Account" round>Account</el-button>
    </router-link>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "BasicHeader",
  computed: {
    // VueX Ïä§ÌÜ†Ïñ¥Ïùò ÏÉÅÌÉúÎ•º Í≥ÑÏÇ∞Îêú ÏÜçÏÑ±ÏúºÎ°ú Í∞ÄÏ†∏Ïò¥.
    isLoggedIn() {
      return this.$store.state.isLoggedIn;
    },
  },
  created() {
    this.checkLoginStatus();
  },
  methods: {
    // Î°úÍ∑∏Ïù∏ ÏÉÅÌÉú ÌôïÏù∏ (Ïø†ÌÇ§)
    async checkLoginStatus() {
      try {
        if (!this.isLoggedIn()) {
          return; // Î°úÍ∑∏Ïù∏ÌïòÏßÄ ÏïäÏùÄ ÏÉÅÌÉúÎùºÎ©¥ Ìï®Ïàò Ï¢ÖÎ£å
        }

        await axios.get(
          `${process.env.VUE_APP_BACKEND_URL}/api/user/check`,
          // Ïø†ÌÇ§ ÏÇ¨Ïö© ÏÑ§Ï†ï
          {
            withCredentials: true,
          }
        );
        this.$store.dispatch("login");
      } catch (error) {
        console.log("ÎØ∏Î°úÍ∑∏Ïù∏ ÏÉÅÌÉú");
      }
    },
    // Î°úÍ∑∏ÏïÑÏõÉ
    async logout() {
      try {
        await axios.post(
          `${process.env.VUE_APP_BACKEND_URL}/api/user/logout`,
          {},
          {
            withCredentials: true,
          }
        );
        alert("Î°úÍ∑∏ÏïÑÏõÉ ÏÑ±Í≥µ!");
        this.$store.dispatch("logout");
        this.$router.push("/login");
      } catch (error) {
        console.error("Error during logout", error);
        alert("Î°úÍ∑∏ÏïÑÏõÉ Ïã§Ìå®");
      }
    },
  },
};
</script>

<style scoped>
h1 {
  color: white;
}
h1:hover {
  scale: 1.1;
}
.custom-router-link {
  text-decoration: none;
}

.el-button {
  margin-left: 5px;
}
</style>