<template>
  <h2>Í∏ÄÏì∞Í∏∞</h2>

  <form @submit.prevent="writeBoard">
    <label for="">Ï†úÎ™©</label>
    <el-input
      v-model="boards.title"
      maxlength="50"
      placeholder="Ï†úÎ™©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî"
      show-word-limit
      type="text"
    />

    <label for="">ÎÇ¥Ïö©</label>
    <el-input
      v-model="boards.content"
      maxlength="2000"
      placeholder="ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî"
      show-word-limit
      type="textarea"
      :rows="10"
      :autosize="{ minRows: 20, maxRows: 200 }"
    />

    <button type="submit" class="submit-button">ÏûëÏÑ±</button>
  </form>
</template>
    
    <script>
import axios from "axios";

export default {
  name: "PageVisitorWrite",
  data() {
    return {
      boards: {
        title: "",
        content: "",
        subCategoryId: "",
      },
    };
  },
  created() {
    this.boards.subCategoryId = this.$route.query.subCategoryId;
  },
  methods: {
    async writeBoard() {
      try {
        const response = await axios.post(
          `${process.env.VUE_APP_BACKEND_URL}/api/post`,
          this.boards,
          {
            withCredentials: true,
          }
        );
        if (response.status === 201) {
          alert("Í≤åÏãúÍ∏Ä ÏûëÏÑ±Ïóê ÏÑ±Í≥µÌñàÏäµÎãàÎã§.");
          this.$router.go(-1);
        }
      } catch (error) {
        let errorMessage = "Í≤åÏãúÍ∏Ä ÏûëÏÑ±Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.";

        if (error.response && error.response.data) {
          errorMessage = Array.isArray(error.response.data.errorMessage)
            ? error.response.data.errorMessage.join("\n")
            : error.response.data.errorMessage || errorMessage;
        }

        alert(errorMessage);
        console.error(error, "Ïïå Ïàò ÏóÜÎäî ÏóêÎü¨ Î∞úÏÉù");
      }
    },
  },
};
</script>
