<template class="visitor-detail-box">
  <span class="visitor-title"> {{ title }} </span>
  <el-page-header @back="goBack" :icon="ArrowLeft">
    <template #content>
      <div class="flex items-center">
        <el-tag>ÏûëÏÑ±Ïûê</el-tag>
        <span> {{ author }} | {{ createdAt }}</span>
      </div>
    </template>
    <template #extra>
      <div class="flex items-center">
        <el-button type="primary" class="ml-2" @click="editContent"
          >Edit</el-button
        >
        <el-button type="danger" class="ml-2" @click="deleteVisitor"
          >delete</el-button
        >
      </div>
    </template>
    <div class="visitor-content" v-if="!editing">
      {{ content }}
    </div>

    <div v-else>
      <el-input
        type="textarea"
        v-model="editableContent"
        rows="4"
        placeholder="Please input your content"
      ></el-input>
      <el-button type="success" class="edit-button" @click="patchVisitor"
        >Save</el-button
      >
    </div>

    <!-- ÎåìÍ∏Ä ÌëúÏãú ÏòÅÏó≠ -->
    <div class="visitor-comments">
      <h3>ÎåìÍ∏Ä</h3>
      <div
        v-for="comment in visitorComments"
        :key="comment.name"
        class="visitor-comment"
      >
        <span><el-tag>admin</el-tag>{{ comment.name }}</span>
        <p>{{ comment.comment }}</p>
      </div>
    </div>
  </el-page-header>
</template>
  
  
<script>
import axios from "axios";

export default {
  name: "PageVisitorDeatil",
  data() {
    return {
      title: "",
      content: "",
      author: "",
      createdAt: "",
      editing: false,
      editableContent: "",
      visitorComments: [],
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    editContent() {
      this.editableContent = this.content; // ÌòÑÏû¨ ÎÇ¥Ïö©ÏùÑ Ìé∏Ïßë Í∞ÄÎä•Ìïú ÎÇ¥Ïö©ÏúºÎ°ú ÏÑ§Ï†ï
      this.editing = true;
    },
    // api ÏöîÏ≤≠ : Î∞©Î™ÖÎ°ù ÏàòÏ†ï
    async patchVisitor() {
      try {
        const visitorId = this.$route.params.visitorId;
        await axios.patch(
          `${process.env.VUE_APP_BACKEND_URL}/api/visitor/${visitorId}`,
          {
            content: this.editableContent,
          },
          {
            withCredentials: true,
          }
        );
        this.content = this.editableContent;
        this.editing = false;
        alert("Î∞©Î™ÖÎ°ù ÏàòÏ†ï ÏôÑÎ£å.");
      } catch (error) {
        console.error("Î∞©Î™ÖÎ°ù ÏàòÏ†ï ÏÑúÎ≤ÑÏóêÎü¨", error);
        alert("Î∞©Î™ÖÎ°ù ÏàòÏ†ï Í∂åÌïúÏù¥ ÏóÜÏäµÎãàÎã§.");
      }
      this.content = this.editableContent;
      this.editing = false;
    },
    // api ÏöîÏ≤≠ : Í≤åÏãúÍ∏Ä ÏÇ≠Ï†ú
    async deleteVisitor() {
      if (confirm("Ï†ïÎßêÎ°ú ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå?")) {
        try {
          const visitorId = this.$route.params.visitorId;
          await axios.delete(
            `${process.env.VUE_APP_BACKEND_URL}/api/visitor/${visitorId}`,
            {
              withCredentials: true,
            }
          );
          alert("Î∞©Î™ÖÎ°ùÏù¥ ÏÇ≠Ï†úÎêòÏóàÏäµÎãàÎã§.");
          this.$router.go(-1);
        } catch (error) {
          console.error("Î∞©Î™ÖÎ°ù ÏÇ≠Ï†ú ÏÑúÎ≤ÑÏóêÎü¨", error);
          alert("Î∞©Î™ÖÎ°ù ÏÇ≠Ï†úÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
        }
      }
    },
  },
  // api ÏöîÏ≤≠ : Î∞©Î™ÖÎ°ù Î†åÎçîÎßÅ
  async created() {
    try {
      const visitorId = this.$route.params.visitorId;
      const response = await axios.get(
        `${process.env.VUE_APP_BACKEND_URL}/api/visitor/${visitorId}`
      );
      this.visitorComments = response.data.visitorComments;
      this.title = response.data.title;
      this.content = response.data.content;
      this.author = response.data.user.name;
      const date = new Date(response.data.createdAt);
      this.createdAt = date.toLocaleDateString();
    } catch (error) {
      console.error("Error fetching visitor detail", error);
    }
  },
};
</script>
<style scoped>
.visitor-title {
  font-weight: bold;
  font-size: 1.5rem;
}
.visitor-content {
  margin: 30px;
  white-space: pre-wrap;
}

.el-page-header {
  margin-top: 20px;
}

.el-tag {
  margin-right: 5px;
}

.edit-button {
  margin-top: 7px;
}
.visitor-comments {
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}
.visitor-comments > h3 {
  margin-top: 20px;
  margin-bottom: 20px;
}

.visitor-comments > div > p {
  margin: 10px;
}

.visitor-comments > div > span {
  font-weight: bold;
  margin-left: 3px;
}
</style>